import cartActiontypes from './cartdrop.actiontype';

import {cartItemss , removeCartItem}  from './cart.utils';

// import {  } from './cartdrop.action';

const INITIAL_STATE ={
    hidden:true,
    cartItem:[]
}
const cartReducer = ( state = INITIAL_STATE , action)=>{
    switch(action.type){
        case cartActiontypes.TOGGLE_CARD_HIDDEN :
            return{
                ...state,
                hidden: !state.hidden 
            }
        case cartActiontypes.ADD_ITEM:
            return{
                ...state,
                cartItem: cartItemss( state.cartItem,action.payload)
            }
        case cartActiontypes.CLEAR_ITEM_FROM_CART:
            return{
                ...state,
                cartItem: state.cartItem.filter( cartitem => cartitem.id !== action.payload.id )
            }
        case cartActiontypes.REMOVE_ITEM:
        return{
            ...state,
            cartItem: removeCartItem(state.cartItem , action.payload)
        }
        default:
            return state; 

    }
}

export default cartReducer;