import React from 'react';

import Custombutton from '../custom-component/customcomponent';

import './cartdropdown.css';

import CartItem from '../cartItem/cart-item';

import {  createStructuredSelector } from 'reselect'

import { connect } from 'react-redux'

import { withRouter } from 'react-router'

import {toggleCartHidden} from '../../redux/cart/cartdrop.action';
// import cartItem  from '../../redux/cart/cartdrop.reducer';
import { SelectCartItems } from '../../redux/cart/cart.selector'
const Cartdropdown = ({cartItem , history , dispatch}) =>(
    <div className='cart-dropdown'>
    <div className='cart-items'>
    {
        cartItem.length ?
        cartItem.map( cartitem =>(<CartItem key={cartitem.id} item={cartitem} />)):
        <span className='empty-message'>your cart is empty</span>
    }
    </div>
    <Custombutton className='button' 
    onClick={()=> 
        {history.push('/checkoutpage');
        dispatch(toggleCartHidden()) 
    }}>Go to checkout</Custombutton>    
    </div>
);



const mapStateToProps = createStructuredSelector({
cartItem :SelectCartItems
})

export default withRouter(connect(mapStateToProps)(Cartdropdown));