import { userActionTypes } from './user.actiontypes'

const INITIAL_STATE = {
    currentUser:null
}

const userReducer = (state = INITIAL_STATE , action)=>{
    switch(action.type){
        case userActionTypes.SET_CURRENT_USER :
            // if the case is set current user this case will implemented
            return{
                ...state,
                currentUser : action.playload
            }
        default: 
        // if there is no state changes regarding this state will remains the same
        return state
    }
}

export default userReducer;