import React from 'react';
import Menulist from '../menu-list/menu-list';
import './directory.css';

import { connect } from 'react-redux';

import {selectDiresctorySection} from '../../redux/diretory/directory.selector'

import {createStructuredSelector} from 'reselect';

const Directory = ({sections})=>(
            <div className="directory-menu">
            {
                sections.map(({id , ...otherSection}) =>
                 (<Menulist key={id} {...otherSection} />
                ))
            }
            </div>
);

const mapStateToProps =createStructuredSelector( {sections: selectDiresctorySection})
export default connect(mapStateToProps)(Directory);