import { combineReducers } from 'redux';

//root reducer is the combination of all the reducer in the application

import userReducer from './user/user.reducer'

import cartReducer from './cart/cartdrop.reducer';

import { persistReducer } from 'redux-persist';

import storage from 'redux-persist/lib/storage';

import directoryReducer from './diretory/directory';

import shopReducer from './SHOP/shop.reducer';


const persistConfig ={
    key: 'root',
    storage,
    whitelist: ['cart']
};

const rootReducer = combineReducers({
        user: userReducer, 
        cart: cartReducer,
        directory: directoryReducer,
        shop: shopReducer 
    // setting up the main user object be storeing the 
    // objects of the userReducer and passing the object of the 
    });

export default persistReducer(persistConfig , rootReducer) 