import React from 'react';

import './checkout.css';

import {connect} from 'react-redux';

import {clearCartItem} from '../../redux/cart/cartdrop.action'

import{ addItem , removeCartItem} from '../../redux/cart/cartdrop.action'

const CheckOutItem = ({cartItem,clearItem,addItem , removeItem}) =>{
    const {imageUrl , name , quantity , price} = cartItem;
    return(
    <div className='checkout-item'>
    <div className='image-container'>
    <img src={imageUrl} alt='item' />
    </div>
    <span className='name'>{name}</span>
    <span className='quantity'>
    <div className='arrow' onClick={()=>{removeItem(cartItem)}}>&#10094;</div>
    <span>
    {quantity}
    </span>
    <div className='arrow' onClick={()=>{addItem(cartItem)}}>&#10095;</div>
    </span>
    <span className='price'>${price}</span>
    <span className='remove' onClick={()=>clearItem(cartItem)}>&#10005;</span>
    </div>
)}

const mapDisptachToProps  = disptach =>({
    clearItem : item => disptach(clearCartItem(item)),
    addItem:item =>disptach(addItem(item)),
    removeItem:item=>disptach(removeCartItem(item))
})

export default connect(null , mapDisptachToProps)(CheckOutItem);