import { createStore , applyMiddleware } from 'redux';

import logger from 'redux-logger';

import rootReducer from './root-reducer';


import { persistStore } from 'redux-persist'

const Middleware =  [logger];
// single logger can be passed diresctly but for big project we need array of logger

// all the objects of the logger is used to craete the store  with 2 parameters 

// one is the rootreducer and other is the middleware object which is being passed to this

export const store = createStore(rootReducer , applyMiddleware(...Middleware))

export const persistor = persistStore(store) 
export default { store , persistor};