import React from "react";
import { Link } from "react-router-dom";
import { ReactComponent as Logo } from "../../asset/logo.svg";

import "./header.css";

import { auth } from "../../firebase/firebase.utils";

import { connect } from "react-redux";

import CartIcon from "../cart-item/cart-icon";

import Cartdropdown from "../cart-dropdown/cartdropdown";

import { createStructuredSelector } from "reselect";

import { SelectCurrentUser } from "../../redux/user/user.selector";

import { SelectCartItemHidden } from "../../redux/cart/cart.selector";

const Header = ({ currentUser, hidden }) => (
  <div className="header">
    <Link to="/ecommerce/" className="logo-container">
      <Logo className="logo" />
    </Link>
    <div className="options">
      <Link to="/shop" className="option">
        SHOP
      </Link>
      <Link to="/contact" className="option">
        CONTACT
      </Link>

      {
        currentUser ? (
          <div className="option" onClick={() => auth.signOut()}>
            SIGN OUT
          </div>
        ) : (
          <Link className="option" to="/signin">
            SIGN IN
          </Link>
        )
        //here onclick tends to signoutfrom the current user from the auth library
      }

      <CartIcon />
    </div>
    {hidden ? null : <Cartdropdown />}
  </div>
);

const mapStateToProps = createStructuredSelector(
  //state is the rootreducer rootreducer.user.currentUser
  {
    currentUser: SelectCurrentUser,
    hidden: SelectCartItemHidden,
    // currentUser from the user from the user reducer
  }
  //we just use the state we are not changint the state
);

//connect will dorectly give the chancen to goive a souped component

export default connect(mapStateToProps)(Header);
