import React from 'react';

import './sign-in.css'

import Forminput from '../form-input/form-input';
// import Signinup from '../../pages/signinandup/signinup';

import Custombutton from '../custom-component/customcomponent'

import  {auth, signInWithGoogle } from '../../../src/firebase/firebase.utils'
class Signin extends React.Component{
    constructor(props){
        super(props)
        this.state={
             email:"",
             password:""
            }
    }

    handleSubmit =async event =>{
        event.preventDefault();
        const{email,password} = this.state;
        try{
            
        await auth.signInWithEmailAndPassword(email,password);
        this.setState({email: "" , password:""})
        }catch(error){
            console.log(error);
        }
    }

    handleChange= event =>{
        const {name , value} = event.target;
        this.setState({ [name]: value})
    }
    
    render(){
        const {email,password} = this.state;
    return(


        <div className='sign-in'>
        <h1 className="title">Sign in</h1>
        <span>signin with your email and password.</span>

        <form onSubmit={this.handleSubmit}>
        <Forminput 
        type="email" 
        value={email} 
        handleChange={this.handleChange} 
        label="email"
        name ="email"
        required />

        <Forminput 
        type="password" 
        value={password}     
        label="password"
        name="password"
        handleChange={this.handleChange} 
        required />
        <div className="buttons">
        <Custombutton type='submit'>Sign in</Custombutton>
        <Custombutton isGoogleSignIn={1} onClick={signInWithGoogle} >Sign in with google</Custombutton>
        </div>
        </form>
        </div>
    )
    }
}

export default Signin;