import React from "react";

import Homepage from "./pages/homepage/homepage";

import { Route } from "react-router-dom";

import Shoppage from "./pages/shop/shoppage";
import "./App.css";
import Header from "./component/header/header";
import { Switch, Redirect } from "react-router";
import Signinup from "./pages/signinandup/signinup";
import { auth, createUserProfileDocument } from "./firebase/firebase.utils";
import { connect } from "react-redux";
import { setCurrentUser } from "../src/redux/user/user.action";
import { createStructuredSelector } from "reselect";
import { SelectCurrentUser } from "./redux/user/user.selector";

import CheckOutPage from "./pages/checkout/checkout.component";

class App extends React.Component {
  // async user=>{
  //   createUserProfileDocument(user)

  unsubdcribedFromAuth = null;

  //     auth.onAuthStateChanged(user =>{
  // // user is the user who signin or signout which is given by the firebase.auth
  //       this.setState({currentuser:user})
  //       //setting up with the user to the currentuser
  //     })
  //when user signed in or signed out
  componentDidMount() {
    const { setCurrentUser } = this.props;
    this.unsubdcribedFromAuth = auth.onAuthStateChanged(async (userAuth) => {
      if (userAuth) {
        const userRef = await createUserProfileDocument(userAuth);

        userRef.onSnapshot((snapShot) => {
          //used instead of this.setstate
          setCurrentUser({
            id: snapShot.id,
            ...snapShot.data(),
          });
        });
      }
      setCurrentUser(userAuth);
    });
  }

  componentWillUnmount() {
    this.unsubdcribedFromAuth();
    //when app closed  sharing of data will be stopped from the server
    //  to the clinetb who is uding the app
  }

  render() {
    const { currentUser } = this.props;
    return (
      <div>
        <Header />
        <Switch>
          <Route exact path="/ecommerce/" component={Homepage} />
          <Route path="/shop" component={Shoppage} />
          <Route exact path="/checkoutpage" component={CheckOutPage} />
          <Route
            extact
            path="/signin"
            render={() => (currentUser ? <Redirect to="/" /> : <Signinup />)}
          />
        </Switch>
      </div>
    );
  }
}

const mapStateToProps = createStructuredSelector({
  currentUser: SelectCurrentUser,
});

const mapDispatchToProps = (dispatch) => ({
  //dispatching the on=bject e=what ever we need

  setCurrentUser: (user) => dispatch(setCurrentUser(user)),

  // updatingte setcurrent user by disptahing the object which is passed over
});

export default connect(mapStateToProps, mapDispatchToProps)(App);

