import { createSelector } from 'reselect';
// input selector
const SelectCart = state => state.cart;

// 1st arg is the input selector
// 2nd is the return type of the selector
export const SelectCartItems = createSelector (
    [SelectCart],
    cart => cart.cartItem
)

export const SelectCartItemHidden = createSelector (
    [SelectCart],
    SelectCart=>SelectCart.hidden
)

export const SelectCartItemsCount = createSelector (
    [SelectCartItems],
    cartItem=>cartItem.reduce((accumulatedQuantity , cartItem) => accumulatedQuantity + cartItem.quantity,0)
)

export const SelectCartTotal = createSelector (
    [SelectCartItems],
    cartItem=>cartItem.reduce((accumulatedQuantity , cartItem) => accumulatedQuantity + cartItem.quantity * cartItem.price ,0)
)