export const createUserProfileDocument = async (userAuth , additionalData)=>{
  if(!userAuth) return;
  const userRef = firestore.doc(`users/${userAuth.uid}`);

  //getting the uerref from the firestore 

  const snapShot = await userRef.get();

  //to get the snappshot get it from the userref

  if(!snapShot.exists){
    //if there is not data prersent 
    const {displayName,email } = userAuth;
    const createdAt = new Date();

    try{
      await userRef.set({
        displayName,
        email,
        createdAt,
        ...additionalData
      }) 
    }catch(error){
      console.log('error creating users',error.message);
    }

  }
  //reyturn this to i=use the data in out application
  return userRef;
};

