export const cartItemss = ( cartItems , cartitemtoadd ) =>{

    const existing = cartItems.find( cartitem => cartitem.id === cartitemtoadd.id)

    if(existing){
        return cartItems.map( cartitem => cartitem.id === cartitemtoadd.id ? {...cartitem , quantity : cartitem.quantity + 1}: cartitem)
        }

    return [...cartItems , {...cartitemtoadd , quantity : 1}]

}

export const removeCartItem = (cartItems , cartItemToRemove) =>{
    const existing = cartItems.find(cartitem=> cartitem.id === cartItemToRemove.id)

    if(existing.quantity === 1){
        return cartItems.filter(cartitem=> cartitem.id !== existing.id )
    }

    return (
        cartItems.map(cartitem => cartitem.id === cartItemToRemove.id ? {...cartitem ,quantity: cartitem.quantity - 1 }: cartitem )
        )

}
