import React from 'react';

import './collectionitem.css';

import Custombutton from '../custom-component/customcomponent'

import { connect } from 'react-redux';

import { addItem } from '../../redux/cart/cartdrop.action'

const Collectionitem = ({item,addItem}) =>{
    const {price,name,imageUrl} = item
    return(

    <div className="collectionitem">
    <div className="image"
    style={{
        backgroundImage: `url(${imageUrl})` 
    }} />

    <div className='collectionfooter'>
    <span className='name'>{name}</span>
    <span className='price'>{price}</span>
    </div>
    <Custombutton className='custom-button' onClick={()=>{addItem(item)}}>Add to cart</Custombutton>
    
    </div>
)};

const mapDisptachToProps = disptach =>({
    addItem : item => disptach(addItem(item))
})

export default connect(null , mapDisptachToProps)(Collectionitem);